import React from 'react';

import {BounceLoader, BeatLoader} from 'react-spinners';

const Loader = () => {
    return (
        <div>
            <div style={{ alignItems: 'center', justifyContent: 'center', marginTop: '20%'}}>
      <center>
      <BounceLoader color='#fff' />
      </center>
      </div>
        </div>
    )
}

export default Loader;