import React from 'react';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Snowfall from 'react-snowfall';


const NewYear = () => {


    const date = new Date();
  const currDate = date.getDate();
  const currMonth = date.getMonth() + 1;
  const currYear = date.getFullYear();

  const newYearBtn = () => toast.success("Enjoy This New Year with Us", {
    position: "top-center",
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: 0.2,});



    return (
        <div>
            {currMonth===1 && currDate===1 ? (<div><p className='greetSpace'><b>ğŸ¥³ ğŸ¾ ğŸ† Happy New Year {currYear} ğŸ¥³ ğŸ•› ğŸ’ ğŸŠ</b></p><button className='btn btn-success wishBtn' onClick={newYearBtn} >Celebrate New Year with Us</button>
        <ToastContainer position="top-center"
autoClose={5000}
hideProgressBar={false}
newestOnTop={false}
closeOnClick
rtl={false}
pauseOnFocusLoss
draggable
pauseOnHover /><Snowfall snowflakeCount={300}/><audio autoPlay='true' preload='auto' loop='true'><source src="" />Your Browser Doesn't Support Audio </audio></div>) : (<div><span></span></div>)}
        </div>
    )
}

export default NewYear;