import React from 'react';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import Snowfall from 'react-snowfall';

const Christmas = () => {

    //const [ spinner, setSpinner ] = useState(true);

    const date = new Date();
  const currDate = date.getDate();
  const currMonth = date.getMonth() + 1;

    const christmasBtn = () => toast.success("Enjoy This Christmas with Us", {
        position: "top-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0.2,});
  
   

    return (
        <div>
            {currMonth===12 && currDate===25 ? (<div><p className='greetSpace'><b>ğŸ… ğŸ„ â„ï¸ Merry Christmas â›„ ğŸ ğŸ…</b></p><button className='btn btn-success wishBtn' onClick={christmasBtn} >Celebrate Christmas with Us</button>
        <ToastContainer position="top-center"
autoClose={5000}
hideProgressBar={false}
newestOnTop={false}
closeOnClick
rtl={false}
pauseOnFocusLoss
draggable
pauseOnHover /><Snowfall snowflakeCount={400}/><audio autoPlay='true' preload='auto' loop='true'><source src="https://www.jesusful.com/wp-content/uploads/music/2020/09/Melissa_Lyons_Shauntia_Flemin_-_We_Wish_You_a_Merry_Christmas_(Jesusful.com).mp3" type="audio/mp3" />Your Browser Doesn't Support Audio </audio></div>) : (<div><span></span></div>)}
        </div>
    )
}

export default Christmas;