import React from 'react';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const MyBirthday = () => {

    const date = new Date();
  const currDate = date.getDate();
  const currMonth = date.getMonth() + 1;


    const notify = () => toast.success("Thank U ... for wishing Me.", {
        position: "top-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0.2,});

    return (
        <div>
            {currMonth===12 && currDate===5 ? (<div><p className='greetSpace'><b>ğŸ¥³ ğŸˆ Hi! Debkanta .... Happy Birth Day ğŸ‚ ğŸ¥³ ğŸ‰</b></p><button className='btn btn-success wishBtn' onClick={notify}>Wish Him</button>
        <ToastContainer position="top-center"
autoClose={5000}
hideProgressBar={false}
newestOnTop={false}
closeOnClick
rtl={false}
pauseOnFocusLoss
draggable
pauseOnHover /><audio autoPlay='true' loop='true'><source src="https://defenceprep.com/wp-content/uploads/2020/11/Happy_Birthday_Song_tae5f4.mp3" type="audio/mp3" /></audio></div>) : (<div><span></span></div>)}
        </div>
    )
}

export default MyBirthday;