import React from 'react';

import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Birthday = () => {

    const date = new Date();
  const currDate = date.getDate();
  const currMonth = date.getMonth() + 1;
  const currYear = date.getFullYear();

  //console.log(currYear);


    const notify = () => toast.success("Celebrate His Birthday with Us", {
        position: "top-center",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: 0.2,});

    return (
        <div>
            {currMonth===1 && currDate===23 ? (<div><p className='greetSpace'><b> 23rd January ... NETAJI's {currYear-1896}th Birthday </b></p><button className='btn btn-success wishBtn' onClick={notify}>Celebrate With Us</button>
        <ToastContainer position="top-center"
autoClose={5000}
hideProgressBar={false}
newestOnTop={false}
closeOnClick
rtl={false}
pauseOnFocusLoss
draggable
pauseOnHover /><audio autoPlay='true' loop='true'><source src="" type="audio/mp3" /></audio></div>) : (<div><span></span></div>)}
        </div>
    )
}

export default Birthday;